<script>

import {mapState} from "vuex";
import Effect from '../background';

export default {
  name: 'HelloWorld',
  components: {},
  props: {
    msg: String
  },
  computed: mapState({
    isDepositModalActive: state => state.isDepositModalActive,
  }),
  mounted() {
    this.vantaEffect = Effect({
      el: this.$refs.vantaRef
    })
    this.x = this.vantaEffect.scene.children[0].tx
    this.y = this.vantaEffect.scene.children[0].ty
    this.z = this.vantaEffect.scene.children[0].tz
  },
  beforeDestroy() {
    if (this.vantaEffect) {
      this.vantaEffect.destroy()
    }
  },
  data: () => ({
    title: "",
    x: 0.,
    y: 0,
    z: 0,
  }),
  watch: {
    // whenever question changes, this function will run
    x() {
      console.log(this.vantaEffect.scene)
      this.vantaEffect.scene.children[0].tx = this.x
    },
    y() {
      console.log(this.vantaEffect.scene)
      this.vantaEffect.scene.children[0].ty = this.y
    },
    z() {
      console.log(this.vantaEffect.scene)
      this.vantaEffect.scene.children[0].tz = this.z
    }
  },
  methods: {
    onTitle(title) {
      this.title = title
    },
    onCloseDeposit() {
      this.$store.commit("isDepositModalActive", false)
    }
  },
}
</script>

